
import { defineComponent, ref, reactive, toRefs } from "vue";
export default defineComponent({
  name: "",
  setup() {
    const data = reactive({
      content: "",
    });
    function open_link() {
      window.open(
        "http://lingyan-website.test.lingyankeyan.com/login",
        "_blank"
      ); // 新窗口打开外链接
    }
    return {
      open_link,
      ...toRefs(data),
    };
  },
});
